<template>
<div class="not-find">
  <div class="not-find-inner">
    <span class="main-num">
      404
    </span>
    <span class="main-content">
      很抱歉,您要访问的页面不存在
    </span>
    <div class="info-box">
      <div class="box">
        <div class="box-title">可能原因:</div>
        <p>1. 输入不正确的网站</p>
        <p>2. 页面已经被移除</p>
        <p>3. 网络不稳定</p>
      </div>
      <div class="box">
        <div class="box-title">可以尝试:</div>
        <p><router-link to="/">1. 返回主页</router-link></p>
        <p><a href="javascript:history.back(-1)">2. 返回上一页</a></p>
        <p><router-link to="/">3. 联系我们</router-link></p>
      </div>
    </div>
  </div>
</div>
</template>
<script>
export default {
  
}
</script>
<style scoped>
.not-find{
  background-color: #F9F9F9;
  height: 100vh;
}
.not-find .not-find-inner{
  margin: 0 auto;
  padding-top: 160px;
  width: 960px;
  min-height: 200px;
  text-align: center;
}
.main-num{
  font-size: 60px;
}
.main-content{
  font-size: 40px;
}
.info-box{
  display: flex;
  margin-top: 20px;
  width: 100%;
  padding: 40px 40px 0 40px;
  border-top: 2px solid #666;
}
.info-box .box{
  width: 50%;
  text-align: left;
}
.box-title{
  font-size: 24px;
}
.info-box .box p,.info-box .box p a{
  margin-top: 26px;
  font-size: 16px;
  color: #555;
}
.info-box .box p a{
  color: #428BCA;
}
</style>



